<template>
    <v-app id="memberforms">
        <v-app-bar
            color="#092a5c"
            class="mobileHeaderContainer"
        >
            <div class="logo-mobile mt-3">
                <router-link to="/" class="navbar-brand">
                    <img
                        :src="logoAft"
                        style="max-height: 42px;"
                    >
                </router-link>
            </div>
            <v-btn
                class="click-me"
                color="white"
                @click="isShow = !isShow"
                icon="mdi:mdi-tune-variant"
            >
            </v-btn>
            <div
                v-show="isShow"
                class="mobileHeaderWrapper d-flex flex-grow-1 px-4"
            >
                <router-link to="/" class="navbar-brand">
                    <img
                        :src="logoAft"
                        style="max-height: 42px;"
                    >
                </router-link>
                <!-- <v-toolbar-title>
                    Membership Forms {{ $route.meta.label }}
                </v-toolbar-title> -->
                <v-spacer />
                <v-autocomplete
                    v-model="selectedAffiliateId"
                    v-model:search="affiliatesSearchText"
                    label="Selected Affiliate"
                    variant="underlined"
                    :items="affiliates"
                    :customFilter="searchAffiliates"
                    :loading="affiliatesSearchLoading"
                    item-title="AffiliateName"
                    item-value="AffiliateId"
                    :placeholder="affiliateText"
                    hide-no-data
                    class="selectAffiliate"
                    @update:model-value="selectAffiliate"
                    @click="loadDefaultAffiliates"
                >
                    <template v-slot:item="{ props, item }">
                        <v-list-item
                            v-bind="props"
                            :title="getAffiliateLabel(item)"
                        ></v-list-item>
                    </template>
                    <template :loader="{ affiliatesSearchLoading }">
                        <v-progress-linear
                            indeterminate
                            height="5"
                            color="#3f98c9"
                            absolute
                        />
                    </template>
                </v-autocomplete>
                <div class="header-btns">
                    <v-menu>
                        <template #activator="{ props }">
                            <v-btn
                                size="small"
                                class="menu-icons"
                                icon="mdi:mdi-backburger"
                                v-bind="props"
                            >
                            </v-btn>
                        </template>
                        <v-list
                            class="mfp-member-nav"
                        >
                            <v-list-item>
                                <v-list-item-title>
                                    <a href="/">
                                        Back to Connect
                                    </a>
                                </v-list-item-title>
                            </v-list-item>
                            <v-list-item @click="logout()">
                                <v-list-item-title>
                                    <span> Logout </span>
                                </v-list-item-title>
                                <form
                                    id="logout-form2"
                                    action="/logout"
                                    method="POST"
                                    class="d-none"
                                >
                                    @csrf
                                </form>
                            </v-list-item>
                        </v-list>
                    </v-menu>
                    <div class="mobile-nav-btns">
                        <v-btn
                            href="/"
                            class="mr-6"
                        >
                            Back to Connect
                        </v-btn>
                        <v-btn
                            class="mr-6"
                            @click="logout()"
                        >
                            Logout
                        </v-btn>
                        <form
                            id="logout-form2"
                            action="/logout"
                            method="POST"
                            class="d-none"
                        >
                            @csrf
                        </form>
                    </div>
                </div>
            </div>
        </v-app-bar>
        <v-main class="main-content">
            <Navigation
                title="Menu"
                :shown="mainDrawer"
                :items="$router.options.routes"
                :member-form-assign-type="memberFormAssignType"
            />
            <v-divider
                vertical
                class="main-divider"
            />
            <router-view />
        </v-main>
        <FooterComponent />
        <v-dialog
            v-model="dialog"
            max-width="290"
        >
          <v-card>
            <v-card-title class="text-h5">
              Changing Affiliate?
            </v-card-title>

            <v-card-text>
              By selecting this affiliate you will lose any changes made to
              an existing form. Are you sure?
            </v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn
                  color="green-darken-1"
                  variant="text"
                  @click="cancelSelectedAffiliate()"
              >
                Cancel
              </v-btn>
              <v-btn
                  color="green-darken-1"
                  variant="text"
                  @click="setSelectedAffiliate()"
              >
                Ok
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
    </v-app>
</template>

<script>
import { debounce } from 'lodash-es';
import api from './api/private';
import Navigation from './src/private/components/nav/Navigation.vue';
import FooterComponent from '../../../../../resources/js/components/Common/FooterComponent.vue';
import logoAft from '../../../../../public/images/logos/mfp-logo-small.png';

export default {
    name: 'PrivateApp',
    components: {
        Navigation,
        FooterComponent,
    },
    data: () => ({
        mainDrawer: true,
        selectedAffiliateId: null,
        affiliates: [],
        affiliatesSearchText: null,
        affiliatesSearchLoading: false,
        dialog: false,
        isShow: false,
        memberFormAssignType: null,
        logoAft,
    }),
    computed: {
        affiliateText() {
            if (this.selectedAffiliate) {
                return (`${this.selectedAffiliate.AffiliateNumber} - ${this.selectedAffiliate.AffiliateName}`);
            }
            return 'Selected Affiliate';
        },
        selectedAffiliate() {
            const affiliate = this.$store.getters['user/selectedAffiliate'];
            console.log({ affiliate })
            if (affiliate) {
                // this.selectedAffiliateId = affiliate.AffiliateId;
            }
            return affiliate;
        },
    },
    watch: {
        affiliatesSearchText(text) {
            this.debounceInput(text);
        },
    },
    beforeMount() {
        this.selectedAffiliateId = this.$store.getters['user/selectedAffiliate'] ? this.$store.getters['user/selectedAffiliate'].AffiliateId : null;
        this.getMemberFormAssignType();
    },
    mounted() {
        if (this.affiliates.length === 0 && this.selectedAffiliate) {
            this.affiliates.push({
                AffiliateId: this.selectedAffiliate.AffiliateId,
                AffiliateName: this.selectedAffiliate.AffiliateName,
                AffiliateNumber: this.selectedAffiliate.AffiliateNumber,
            });
        }
    },
    created() {
        this.debounceInput = debounce(function (search) {
            if (this.selectedAffiliate && search === this.selectedAffiliate.AffiliateName) {
                return;
            }
            this.affiliatesSearchLoading = true;
            api.loadDefaultAffiliates({
                search,
            })
                .then((response) => {
                    this.affiliates = response.data.data;
                    if (response.data.meta.last_page > 1) {
                        this.affiliates.push({ AffiliateName: '<strong style="color:#092a5c;">Click to see all affiliates here >></strong>', AffiliateNumber: '', AffiliateId: -1 });
                    }
                    this.affiliatesSearchLoading = false;
                });
        }, 500);
    },
    methods: {
        logout() {
            document.getElementById('logout-form2').submit();
        },
        selectAffiliate(affiliateId) {
            if (affiliateId > 0) {
                this.selectedAffiliateId = affiliateId;
                if (this.isFormWorkInProgress()) {
                    this.dialog = true;
                } else {
                    // window.location.reload();
                    this.setSelectedAffiliate();
                }
            } else {
                this.$nextTick(() => {
                    this.selectedAffiliateId = this.$store.getters['user/selectedAffiliate'] ? this.$store.getters['user/selectedAffiliate'].AffiliateId : null;
                });
            }
        },
        setSelectedAffiliate() {
            this.dialog = false;
            localStorage.mfpAffiliateId = this.selectedAffiliateId;
            localStorage.formBuilderActiveTemplate = JSON.stringify({});
            this.$store.dispatch('user/setUserSelectedEntity', this.selectedAffiliateId).then(() => {
                if (window.location.href.endsWith('edit')) {
                    this.$router.push({ path: 'manage' });
                } else {
                    window.location.reload();
                }
            });
        },
        cancelSelectedAffiliate() {
            this.dialog = false;
            this.selectedAffiliateId = localStorage.mfpAffiliateId;
            window.location.reload();
        },
        loadDefaultAffiliates() {
            const affiliateName = this.selectedAffiliate.AffiliateName;
            if (this.affiliates.length === 1 && this.affiliates.pop().AffiliateName === affiliateName) {
                api.loadDefaultAffiliates({
                    search: '',
                })
                    .then((response) => {
                        if (this.affiliates.indexOf((affiliate) => affiliate.AffiliateName === affiliateName) !== -1) {
                            this.affiliates = [];
                        }
                        this.affiliates = this.affiliates.concat(response.data.data);
                        if (response.data.meta.last_page > 1) {
                            this.affiliates.push({ AffiliateName: '<strong style="color:#092a5c;">Click to see all affiliates here >></strong>', AffiliateNumber: '', AffiliateId: -1 });
                        }
                    });
            }
        },
        getMemberFormAssignType() {
            this.loading = true;
            this.affiliateId = this.selectedAffiliate
                ? this.selectedAffiliate.AffiliateId : null;
            api.getMemberFormAssign(this.affiliateId)
                .then((response) => {
                    const memberFormAssign = response.data;
                    if (memberFormAssign) {
                        this.memberFormAssignType = memberFormAssign.type;
                        console.log(this.memberFormAssignType);
                    }
                    this.loading = false;
                });
        },
        // searchAffiliates(item, queryText) {
        //     const searchText = queryText.toLowerCase();
        //     const nameIndex = item.AffiliateName.toLowerCase().indexOf(searchText);
        //     const numberIndex = item.AffiliateNumber.toLowerCase().indexOf(searchText);
        //     return item.AffiliateId < 0 || nameIndex > -1 || numberIndex > -1;
        // },
        searchAffiliates(itemTitle, queryText, item) {
            const searchText = queryText.toLowerCase();
            const id = item.raw.AffiliateId;
            if (id < 0) {
                // @todo: possible bug?
                return true;
            }
            const title = itemTitle.toLowerCase();
            const number = item.raw.AffiliateNumber.toLowerCase();
            return title.indexOf(searchText) > -1 || number.indexOf(searchText) > -1;
        },
        getAffiliateLabel(item) {
            return `${item.raw.AffiliateName}${item.raw.AffiliateId > 0 ? ` (${item.raw.AffiliateNumber})` : ''}`;
        },
        isFormWorkInProgress() {
            const url = new URL(window.location.href);
            const isExpectedFormRoute = (url.pathname.endsWith('create') || url.pathname.endsWith('edit'));
            const localStorageActiveTemplate = localStorage.formBuilderActiveTemplate
                ? JSON.parse(localStorage.formBuilderActiveTemplate) : null;
            return (isExpectedFormRoute && localStorageActiveTemplate && localStorageActiveTemplate.step > 1);
        },
    },
};
</script>

